<template>
  <div class="page1">
    <h2>前端博客</h2>
    <div class="gray-left-line">
      <p>一个前端工程师的作品集，里面有我开发过程中的思考和尝试。这其中不乏有一些“轮子”作品，但只要你足够好奇就会发现，他们都是独一无二的。如果不是，那就算了。也有一些有意思的前端技术应用，他们都在《代码随笔》里，通常是些练手的小玩意，不完整，也不需要完整。</p>
    </div>
    <router-link to="page2"><button class="layui-btn layui-btn-normal">Click me&Go page2</button></router-link>
  </div>
</template>

<script>
export default {
  name: 'Page1',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<style>
.page1 {
  position: relative;
  margin: 0 auto;
  padding: 50px 0;
  width: 100%;
  text-align: center;
}
</style>



