<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
* {
  box-sizing: border-box;
}
#out-main {
  min-height: 100vh;
}
.container {
  position: relative;
  margin: 0 auto;
  padding: 50px 0;
  width: 800px;
  text-align: center;
}
.gray-left-line {
  position: relative;
  right: 20px;
  margin: 0 auto;
  padding-left: 20px;
  width: 80%;
  line-height: 24px;
  text-align: left;
  border-left: 3px solid #C0C4CC;
}
</style>



